import axios from "../../config/axios";

const getTotalData = (tl) => {
  return { type: "GET_TOTAL_DATA", payload: tl };
};

const startGetTotalData = () => {
  return (dispatch) => {
    // console.log(id,"fse_id_redux")

    const loadData = async () => {
      try {
        const TotalData=await axios.post("/lead_stats")
        // console.log(TotalData.data,"fse_list_redux")
        dispatch(getTotalData(TotalData.data.dates))
        
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
};



export { getTotalData, startGetTotalData  };
